import { useEffect, useMemo, useState } from 'react';
import { useAccount, usePublicClient } from 'wagmi';
import { formatUnits } from 'viem';
import addresses from '@lib/contracts/addresses.json';
import { activityLoggerAbi } from '@lib/contracts/abi/activityLogger';

type ActionType = 0 | 1 | 2 | 3 | 4 | 5 | 6 | 7;

const ACTION_LABELS: Record<ActionType, string> = {
  0: 'Deposit',
  1: 'Withdraw',
  2: 'Buy Session',
  3: 'Play Round',
  4: 'Pending Reward',
  5: 'Reward Claimed',
  6: 'Reward Forfeited',
  7: 'Leopard Bonus',
};

export interface GameHistoryEntry {
  txHash: `0x${string}`;
  action: string;
  gameId: number;
  amount: string;
  rounds: number;
  diceValues: number[];
  diceSum: number;
  isBaozi: boolean;
  isClockwise: boolean;
  startPosition: number;
  endPosition: number;
  payout: string;
  timestamp?: number;
  blockNumber: bigint;
}

export function useGameHistory() {
  const { address } = useAccount();
  const publicClient = usePublicClient();
  const [history, setHistory] = useState<GameHistoryEntry[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const loggerAddress = useMemo(
    () => addresses.contracts.GameActivityLogger as `0x${string}`,
    [],
  );

  useEffect(() => {
    if (!address || !publicClient || !loggerAddress || loggerAddress === '0x0000000000000000000000000000000000000000') {
      setHistory([]);
      return;
    }

    let isMounted = true;
    let unwatch: (() => void) | undefined;

    const parseLog = async (log: any): Promise<GameHistoryEntry | null> => {
      if (!log.args) return null;
      const actionIdx = Number(log.args.action ?? 0) as ActionType;
      const diceValuesRaw = Array.isArray(log.args.diceValues)
        ? (log.args.diceValues as readonly (bigint | number)[])
        : [];
      const block = log.blockHash
        ? await publicClient.getBlock({ blockHash: log.blockHash })
        : undefined;

      return {
        txHash: log.transactionHash!,
        action: ACTION_LABELS[actionIdx] ?? 'Unknown',
        gameId: Number(log.args.gameId ?? 0),
        amount: formatUnits(log.args.amount ?? 0n, 6),
        rounds: Number(log.args.rounds ?? 0),
        diceValues: diceValuesRaw.map((value) => Number(value)),
        diceSum: Number(log.args.diceSum ?? 0),
        isBaozi: Boolean(log.args.isBaozi),
        isClockwise: Boolean(log.args.isClockwise),
        startPosition: Number(log.args.startPosition ?? 0),
        endPosition: Number(log.args.endPosition ?? 0),
        payout: formatUnits(log.args.payout ?? 0n, 6),
        timestamp: log.args.timestamp ? Number(log.args.timestamp) : block ? Number(block.timestamp) : undefined,
        blockNumber: log.blockNumber ?? 0n,
      } satisfies GameHistoryEntry;
    };

    const fetchHistory = async () => {
      try {
        setIsLoading(true);
        setError(null);

        const logs = await publicClient.getContractEvents({
          address: loggerAddress,
          abi: activityLoggerAbi,
          eventName: 'ActivityLogged',
          fromBlock: 0n,
          ...(address ? { args: { player: address } as const } : {}),
        });

        const entries = (await Promise.all(logs.map(parseLog))).filter((entry): entry is GameHistoryEntry => entry !== null);
        if (!isMounted) return;
        const sorted = entries.sort((a, b) => Number(b.blockNumber - a.blockNumber));
        setHistory(sorted);
      } catch (err: any) {
        if (!isMounted) return;
        console.error('Failed to fetch history', err);
        setError(err?.shortMessage || err?.message || 'Failed to load history');
      } finally {
        if (isMounted) {
          setIsLoading(false);
        }
      }
    };

    fetchHistory();

    unwatch = publicClient.watchContractEvent({
      address: loggerAddress,
      abi: activityLoggerAbi,
      eventName: 'ActivityLogged',
      ...(address ? { args: { player: address } as const } : {}),
      onLogs: async (logs) => {
        const parsed = (await Promise.all(logs.map(parseLog))).filter((entry): entry is GameHistoryEntry => entry !== null);

        setHistory((prev) => {
          const existingHashes = new Set(prev.map((entry) => entry.txHash));
          const merged = [...parsed.filter((entry) => !existingHashes.has(entry.txHash)), ...prev];
          return merged.sort((a, b) => Number(b.blockNumber - a.blockNumber));
        });
      },
    });

    return () => {
      isMounted = false;
      unwatch?.();
    };
  }, [address, publicClient, loggerAddress]);

  return {
    history,
    isLoading,
    error,
  };
}
