import { ReactNode } from 'react';
import { AnimatePresence, motion } from 'framer-motion';

interface ModalProps {
  isOpen: boolean;
  title: string;
  onClose: () => void;
  children: ReactNode;
  footer?: ReactNode;
  widthClassName?: string;
}

export function Modal({ isOpen, title, onClose, children, footer, widthClassName = 'max-w-lg' }: ModalProps) {
  return (
    <AnimatePresence>
      {isOpen && (
        <motion.div
          className="fixed inset-0 z-50 flex items-center justify-center"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <motion.div
            className="absolute inset-0 bg-slate-950/70 backdrop-blur"
            onClick={onClose}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          />

            <motion.div
              className={`relative z-10 w-full ${widthClassName} mx-4`}
              initial={{ opacity: 0, scale: 0.95, y: 20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.95, y: 20 }}
              transition={{ type: 'spring', stiffness: 260, damping: 20 }}
            >
              <div className="card p-6 shadow-xl border border-white/5 bg-gradient-to-br from-slate-900 via-slate-900/95 to-slate-950">
                <div className="flex items-start justify-between mb-4">
                  <h3 className="text-xl font-semibold text-white">{title}</h3>
                  <button
                    onClick={onClose}
                    className="text-slate-400 hover:text-slate-200 transition-colors"
                    aria-label="Close modal"
                  >
                    
                  </button>
                </div>

                <div className="text-slate-300 space-y-4">
                  {children}
                </div>

                {footer && (
                  <div className="mt-6 flex flex-col-reverse gap-3 sm:flex-row sm:justify-end">
                    {footer}
                  </div>
                )}
              </div>
            </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
}
