import { motion } from 'framer-motion';
import { Dice3D } from '@components/ui/Dice3D';
import { AnimatedNumber } from '@components/ui/animated-number';
import './DiceRollerView.css';

export interface DiceRollerViewProps {
  diceSlots: number[];
  diceSum: number;
  isBaozi: boolean;
  isRolling: boolean;
  showStats?: boolean;
  disabled?: boolean;
  onRoll?: () => void;
}

export function DiceRollerView({
  diceSlots,
  diceSum,
  isBaozi,
  isRolling,
  showStats = true,
  disabled = false,
  onRoll,
}: DiceRollerViewProps) {
  return (
    <motion.div
      className="dice-roller-card"
      initial={{ opacity: 0, y: 24 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.45, ease: 'easeOut' }}
    >
      <div className="dice-roller-card__header">
        <div>
          <p className="dice-roller-card__eyebrow">LuckChain Multi-Chain Dice</p>
          <h2 className="dice-roller-card__title">Dice Roller</h2>
          <p className="dice-roller-card__subtitle">Feel the toss. Own the roll.</p>
        </div>
        <div className="dice-roller-card__badge">
          <span className="dice-roller-card__badge-label">VRF Powered</span>
        </div>
      </div>

      <div className="dice-roller-card__faces">
        {diceSlots.map((value, index) => (
          <div key={`${index}-${value}`} className="dice-roller-card__slot">
            <Dice3D value={value} />
          </div>
        ))}
      </div>

      {!isRolling && showStats && (
        <div className="dice-roller-card__stats">
          <div>
            <p className="dice-roller-card__stats-label">Total Sum</p>
            <p className="dice-roller-card__stats-value">
              <AnimatedNumber value={diceSum} />
            </p>
          </div>
          <div className="dice-roller-card__divider" />
          <div>
            <p className="dice-roller-card__stats-label">Starting Cell</p>
            <p className="dice-roller-card__stats-meta">
              <AnimatedNumber value={diceSum} />  Move{' '}
              <AnimatedNumber value={Math.max(diceSum - 1, 0)} /> steps
            </p>
          </div>
        </div>
      )}

      {isBaozi && !isRolling && (
        <motion.div
          className="dice-roller-card__baozi"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ type: 'spring', stiffness: 320, damping: 18 }}
        >
            (Leopard)! All {diceSlots[0]}s!
        </motion.div>
      )}

      {onRoll && (
        <button
          onClick={onRoll}
          disabled={isRolling || disabled}
          className={`dice-roller-card__button ${
            isRolling || disabled ? 'dice-roller-card__button--disabled' : ''
          }`}
        >
          {isRolling ? (
            <span className="dice-roller-card__loading">
              <div className="dice-roller-card__spinner" />
              Rolling...
            </span>
          ) : (
            ' Roll Dice'
          )}
        </button>
      )}
    </motion.div>
  );
}
